/*@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,600,700&subset=latin-ext');*/
.ms-parent {
  display: inline-block;
  position: relative;
  vertical-align: middle;
}

.ms-parent.form-control {
  padding: 0;
  /* z-index: 10; */
}

.ms-choice {
  display: block;
  width: 100%;
  height: 100%;
  padding: 0;
  overflow: hidden;
  cursor: pointer;
  border: none;
  box-shadow: none;
  text-align: left;
  white-space: nowrap;
  line-height: 30px;
  color: #252525;
  text-decoration: none;
  background-color: #fff;
}

@media (max-width: 767px) {
  .ms-choice {
    height: 34px;
  }
}

.ms-choice.disabled {
  background-color: #f4f4f4;
  background-image: none;
  border: 1px solid #ddd;
  cursor: default;
}

.ms-choice>span {
  position: absolute;
  height: 32px;
  top: 0;
  left: 0;
  right: 20px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: block;
  padding-left: 14px;
}

.ms-choice>span.placeholder {
  color: #555;
  font-family: 'Open Sans', arial;
  font-size: 12px;
}

.ms-choice>div {
  position: absolute;
  top: 0;
  right: 0;
  width: 20px;
  height: 100%;
}

.ms-choice>div::before {
  position: relative;
  top: 1px;
  display: inline-block;
  content: "\e259";
  color: inherit;
  font-family: 'Glyphicons Halflings';
  font-style: normal;
  font-weight: 400;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 10px;
  color: #000;
}

.ms-choice>div.open::before {
  content: "\e260";
}

.ms-drop {
  width: 100%;
  overflow: hidden;
  display: none;
  margin-top: -1px;
  padding: 0 5px 0;
  position: absolute;
  z-index: 1000;
  background: #fff;
  color: #252525;
  border: 1px solid #aaa;
  border-bottom-left-radius: 2px;
  border-bottom-right-radius: 2px;
}

.ms-drop.bottom {
  top: 100%;
  box-shadow: 0 4px 5px rgba(0, 0, 0, .15);
}

.ms-drop.top {
  bottom: 100%;
  box-shadow: 0 -4px 5px rgba(0, 0, 0, .15);
}

.ms-search {
  display: inline-block;
  margin: 0;
  min-height: 26px;
  padding: 4px;
  position: relative;
  white-space: nowrap;
  width: 100%;
  z-index: 10000;
}

.ms-search input {
  width: 100%;
  height: auto !important;
  min-height: 24px;
  padding: 0 20px 0 5px;
  margin: 0;
  outline: 0;
  font-family: sans-serif;
  font-size: 1em;
  border: 1px solid #aaa;
  border-radius: 0;
  box-shadow: none;
}

.ms-search, .ms-search input {
  box-sizing: border-box;
}

.ms-drop ul {
  overflow: auto;
  margin: 0;
  padding: 5px 8px;
}

.ms-drop ul>li {
  list-style: none;
  display: list-item;
  background-image: none;
  position: static;
}

.ms-drop ul>li .disabled {
  opacity: .35;
}

.ms-drop ul>li.multiple {
  display: block;
  float: left;
}

.ms-drop ul>li.group {
  clear: both;
}

.ms-drop ul>li.multiple label {
  width: 100%;
  display: block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.ms-drop ul>li label {
  font-weight: normal;
  display: block;
  white-space: nowrap;
}

.ms-drop ul>li label span {
  margin-left: 3px;
}

.ms-drop ul>li label.optgroup {
  font-weight: bold;
}

.ms-drop input[type="checkbox"] {
  vertical-align: middle;
}

.ms-drop .ms-no-results {
  display: none;
}
